.cta {
  width: 100%;
  margin-bottom: 160px;

  .bx {
    border: 1px solid green;
    display: flex;
    overflow: hidden;

    &.str {
      flex-direction: row;
      border-radius: 0 0 0 65px;
    }

    &.opp {
      flex-direction: row-reverse;
      border-radius: 0 0 65px 0;
    }

    .l1 {
      width: 50%;
      min-height: 560px;
      text-align: left;
      padding: 0% 6% 0% 6%;
      background-color: var(--e-global-color-4a043de);
      display: flex;
      flex-direction: column;
      justify-content: center;

      .t1 {
        color: var(--e-global-color-primary);
        margin-bottom: 35px;
        font-size: 36px;
        font-weight: 500;
      }

      .tx {
        color: var(--e-global-color-text);
      }
    }

    .r1 {
      width: 50%;
      background-size: cover;
      background-position: center;
    }
  }
}

@media (max-width: 1300px) {
  .cta {
    padding: 0 5%;

    .bx {
      display: flex;
      flex-direction: column !important;

      .l1 {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 5% 0 5% !important;

        .t1 {
          margin-bottom: 35px;
          font-size: 36px;
          font-weight: 500;
        }

        .tx {
          color: var(--e-global-color-text);
        }
      }

      .r1 {
        width: 100%;
        height: 300px;
        background-size: cover;
        background-position: center;
      }
    }
  }
}

@media (max-width: 992px) {
  .cta {
    padding: 0 5%;

    .bx {
      display: flex;
      flex-direction: column !important;

      .l1 {
        min-height: unset;
        padding: 30px 5% !important;

        .t1 {
          font-size: 24px;
          margin-bottom: 20px;
        }

        .tx {
          color: var(--e-global-color-text);
          font-size: 12px;
        }
      }

      .r1 {
        width: 100%;
        height: 300px;
        background-size: cover;
        background-position: center;
      }
    }
  }
}
